.glide {
  position: relative;
  width: 100%;
  box-sizing: border-box;
}

.glide * {
  box-sizing: inherit;
}

.glide__track {
  overflow: hidden;
}

.glide__slides {
  position: relative;
  width: 100%;
  list-style: none;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  touch-action: pan-Y;
  overflow: hidden;
  padding: 0;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  will-change: transform;
}

.glide__slides--dragging {
  user-select: none;
}

.glide__slide {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  white-space: normal;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

.glide__slide a {
  user-select: none;
  -webkit-user-drag: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.glide__arrows {
  -webkit-touch-callout: none;
  user-select: none;
}

.glide__bullets {
  -webkit-touch-callout: none;
  user-select: none;
}

.glide--rtl {
  direction: rtl;
}

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}

*::before, *::after {
  box-sizing: inherit;
}

body {
  position: relative;
  overflow-x: hidden;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

.navbar-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2rem 0;
  z-index: 2;
}

.navbar-container .container {
  display: flex;
  justify-content: space-between;
}

.navbar-container .container .menu-btn {
  position: absolute;
  z-index: 3;
  right: 35px;
  top: 35px;
  cursor: pointer;
  transition: all .3s ease-out;
}

.navbar-container .container .menu-btn .btn-line {
  display: block;
  width: 38px;
  height: 3px;
  margin: 0 0 6.2px;
  border-radius: 15%;
  background-color: #000;
  transition: all .3s ease-out;
}

.navbar-container .container .menu-btn .btn-line:nth-of-type(2) {
  width: 27px;
}

.slider .glide__track .glide__slides {
  margin: 0;
}

.slider .glide__track .glide__slides .glide__slide .slide__image {
  width: 100vw;
  height: 100vh;
}

.slider .glide__track .glide__slides .glide__slide .slide__image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.slider .glide__arrows .glide__arrow {
  position: absolute;
  display: block;
  top: 50%;
  z-index: 2;
  background-color: transparent;
  opacity: 1;
  cursor: pointer;
  transition: all 150ms ease-in-out;
  transform: translateY(-50%);
  line-height: 1;
  border: none;
  opacity: .5;
}

.slider .glide__arrows .glide__arrow:focus {
  outline: none;
}

.slider .glide__arrows .glide__arrow:hover {
  opacity: 1;
}

.slider .glide__arrows .glide__arrow--left {
  left: 1rem;
  transform: translateY(-50%) rotate(180deg);
}

.slider .glide__arrows .glide__arrow--right {
  right: 1rem;
}

.slider .glide__arrows .glide__arrow img {
  width: 2.5rem;
}
